import React, {useContext} from "react";
import {GlobalContext} from "../context/GlobalState";

export const MovieControls = ({movie, type}) => {
    const {
        removeMovieFromWatchlist,
        addMovieToWatched,
        removeMovieFromWatchedToWatchlist,
        removeMovieFromWatched,
    } = useContext(GlobalContext);


    return (
        <div className="inner-card-controls">
            {type === 'watchlist' && (
                <>
                    <button className="ctrl-btn" onClick={() => addMovieToWatched(movie)}>
                        <i className="fa-fw far fa-eye"/>
                    </button>

                    <button className="ctrl-btn" onClick={() => removeMovieFromWatchlist(movie.id)}>
                        <i className="fa-fw fa fa-times"/>
                    </button>
                </>
            )}

            {type === 'watched' && (
                <>
                    <button className="ctrl-btn" onClick={() => removeMovieFromWatchedToWatchlist(movie)}>
                        <i className="fa-fw far fa-eye-slash"/>
                    </button>

                    <button className="ctrl-btn" onClick={() => removeMovieFromWatched(movie.id)}>
                        <i className="fa-fw fa fa-times"/>
                    </button>
                </>
            )}
        </div>
    );
};